  gap: 4.375rem;
}
@media screen and (min-width: 768px) {
  .flow__list {
    margin-top: 11.25rem;
    gap: 7.5rem;
  }
}

.flow__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas: "image" "content";
  gap: 1.875rem;
}
@media screen and (min-width: 768px) {
  .flow__item {
    grid-template-columns: calc((584 * 100) / var(--cq-ref) * 1cqw) 1fr;
    grid-template-areas: "image content";
    gap: clamp(9px, 90 * 100 / var(--cq-ref) * 1cqw, 190px);
  }
}

@media screen and (min-width: 768px) {
  .flow__item:nth-child(odd) {
    margin-left: calc(50% - 50cqw);
  }
}

@media screen and (min-width: 768px) {
  .flow__item:nth-child(even) {
    grid-template-columns: 1fr calc((584 * 100) / var(--cq-ref) * 1cqw);
    grid-template-areas: "content image";
    margin-right: calc(50% - 50cqw);
  }
}

.flow__item-content {
  grid-area: content;
  max-width: 780px;
}

.flow__item-img-wrap {
  grid-area: image;
}
@media (max-width: 767px) {
  .flow__item-img-wrap {
    margin-inline: calc(50% - 50cqw);
  }
}

.flow__item-img {
  height: auto;
  object-fit: cover;
  aspect-ratio: 564/376;
}

